.animation_skip {
    visibility: hidden;
    position: absolute;
    width: 10vh;
    height: 10vh;
    top: 2vh;
    left: 2vh;

    &__button {
        width: 100%;
        height: 100%;
        background-color: #fff;
        color: #3dbacc;
        font-size: 4vh;
        border-radius: 50%;
        border: 5px solid #fff;
        cursor: pointer;
        transition: .2s;
        @media (orientation: portrait) {
            height: 20vw;
            width: 20vw;
            top: 2vw;
            left: 2vw;
            font-size: 8vw; }

        &:hover {
            background-color: #E7655C;
            color: #fff;
            transform: scale(1.1); } } }
