.plate_input {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    &__result_simple {
        margin-top: 6%;
        margin-right: 30%;
        color: #3dbacc;
        font-size: 3vh;
        text-align: right;
        @media (orientation: portrait) {
            font-size: 3.4vw;
            margin-top: 9%;
            margin-right: 29%; } } }
